import { useState, useEffect, useCallback } from 'react';
import useIsMountedRef from './useIsMountedRef';
import chopExpiredSettingService from '../services/chopExpiredSettingService';

export default function useChopExpiredSetting() {
  const [chopExpiredSettings, setChopExpiredSettings] = useState([]);
  const isMountedRef = useIsMountedRef();

  const getChopExpiredSettings = useCallback(async () => {
    const data = await chopExpiredSettingService.list();
    setChopExpiredSettings(data.data.data);
  }, [isMountedRef]);

  const deleteChopExpiredSetting = useCallback(
    async (id) => {
      await chopExpiredSettingService.delete(id);
      getChopExpiredSettings();
    },
    [getChopExpiredSettings]
  );

  useEffect(() => {
    getChopExpiredSettings();
  }, [getChopExpiredSettings]);

  return { chopExpiredSettings, deleteChopExpiredSetting };
}
