import axios from '../utils/axios';

class ChopExpiredSettingService {
  list = async () => {
    const chopExpiredSettings = await axios.get('/api/v1/chopExpiredSettings');
    return chopExpiredSettings;
  };

  create = async (data) => {
    const chopExpiredSetting = await axios.post('/api/v1/chopExpiredSettings', data);
    return chopExpiredSetting;
  };

  get = async (id) => {
    const chopExpiredSetting = await axios.get(`/api/v1/chopExpiredSettings/${id}`);
    return chopExpiredSetting;
  };

  update = async (id, data) => {
    const chopExpiredSetting = await axios.patch(`/api/v1/chopExpiredSettings/${id}`, data);
    return chopExpiredSetting;
  };

  delete = async (id) => {
    const chopExpiredSetting = await axios.delete(`/api/v1/chopExpiredSettings/${id}`);
    return chopExpiredSetting;
  };
}

const chopExpiredSettingService = new ChopExpiredSettingService();

export default chopExpiredSettingService;
