import React, { useState, useCallback, useEffect } from 'react';
import { Box, Container, makeStyles } from '@material-ui/core';
import Page from '../../../components/Page';
import useRank from '../../../hooks/useRank';
import useIsMountedRef from '../../../hooks/useIsMountedRef';
import Header from './Header';
import { useParams } from 'react-router-dom';
import ChopsExpiredSettingEditForm from './ChopsExpiredSettingEditForm';
import useChopExpiredSetting from '../../../hooks/useChopExpiredSetting';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

function ChopsExpiredSetting() {
  const classes = useStyles();
  const { chopExpiredSettings } = useChopExpiredSetting();
  const { t } = useTranslation();

  if (!chopExpiredSettings.length) {
    return null;
  }

  return (
    <Page className={classes.root} title={t('Chops Expired Setting')}>
      <Container maxWidth="lg">
        <Header />
        <Box mt={3}>
          <ChopsExpiredSettingEditForm chopExpiredSetting={chopExpiredSettings[0]} />
        </Box>
      </Container>
    </Page>
  );
}

export default ChopsExpiredSetting;
