import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import * as Yup from 'yup';
import { Formik } from 'formik';
import useSnackbar from '../../../hooks/useSnackbar';
import {
  Box,
  Button,
  Card,
  CardContent,
  Grid,
  Switch,
  TextField,
  Typography,
  makeStyles
} from '@material-ui/core';
import { Alert } from '@material-ui/lab';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import chopExpiredSettingService from '../../../services/chopExpiredSettingService';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles(() => ({
  root: {}
}));

function ChopsExpiredSettingEditForm({ className, chopExpiredSetting, ...rest }) {
  const classes = useStyles();
  const { successSnackbar } = useSnackbar();
  const { t } = useTranslation();

  return (
    <Formik
      initialValues={{
        expired_date: chopExpiredSetting.expired_date || 365
      }}
      validationSchema={Yup.object().shape({
        expired_date: Yup.number()
      })}
      onSubmit={async (values, { resetForm, setErrors, setStatus, setSubmitting }) => {
        try {
          await await chopExpiredSettingService.update(chopExpiredSetting.id, {
            ...values,
            id: chopExpiredSetting.id
          });
          setStatus({ success: true });
          setSubmitting(false);
          successSnackbar(t('Setting updated'));
        } catch (error) {
          setStatus({ success: false });
          setErrors({ submit: error.response.data.message });
          setSubmitting(false);
        }
      }}
    >
      {({ errors, handleBlur, handleChange, handleSubmit, isSubmitting, touched, values }) => (
        <form className={clsx(classes.root, className)} onSubmit={handleSubmit} {...rest}>
          <Card>
            <CardContent>
              {errors.submit && (
                <Box mb={3}>
                  <Alert severity="error">{errors.submit}</Alert>
                </Box>
              )}
              <Grid container spacing={3}>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.expired_date && errors.expired_date)}
                    fullWidth
                    helperText={t('Expired after x days')}
                    label={t('Expired Date')}
                    name="expired_date"
                    type="number"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.expired_date}
                    variant="outlined"
                  />
                </Grid>
              </Grid>
              <Box mt={2}>
                <Button variant="contained" color="secondary" type="submit" disabled={isSubmitting}>
                  {t('Submit')}
                </Button>
              </Box>
            </CardContent>
          </Card>
        </form>
      )}
    </Formik>
  );
}

ChopsExpiredSettingEditForm.propTypes = {
  className: PropTypes.string,
  chopExpiredSetting: PropTypes.object.isRequired
};

export default ChopsExpiredSettingEditForm;
